<template>
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">
                        {{$t('Approve/Reject Request')}}
                    </h5>
                    <button v-on:click="CloseApproveRejectopup($event)" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                </div>
                <div class="modal-body">
                    <div class="listing">
                        <div class="table-responsive tableborder" style="overflow-x:auto;">
                            <table class="table-sorting table table-bordered" id="table-openactivities-content" style="width:1500px !important;">
                                <thead class="thead-bg">
                                    <tr>
                                        <th class="text-center">
                                            <div class="custom-control custom-checkbox" @click="checkSelectedRecord()">
                                                <input class="chkItems custom-control-input" v-model="SelectAll" type="checkbox" id="chkAll" name="checkbox"><label class="custom-control-label universal-custom-control-label pt-1"></label>
                                            </div>
                                        </th>

                                        <th><span>User Name</span></th>
                                        <th><span>Product</span></th>
                                        <th><span>Unique Name</span></th>
                                        <th><span>Reason</span></th>
                                        <th><span>Comment</span></th>
                                        <th><span>Approval Comment</span></th>
                                        <th><span>Status</span></th>
                                        <th><span>Quantity</span></th>
                                        <th><span>Product Cost</span></th>
                                        <th><span>Book Value</span></th>
                                        <th><span>Overdue days</span></th>
                                        <th><span>Penalty Status</span></th>
                                        <th><span>Penalty </span></th>
                                        <th><span>Return Date </span></th>
                                        <th><span>Penalty Amount</span></th>

                                    </tr>
                                </thead>

                                <tbody>
                                    <tr v-for="(item,index) in AssetReplacelistData" :key="item.ASSET_ASSIGNMENT_ID" :index=index>
                                        <td class="text-center">
                                            <div v-if="item.STATUS_ID == 1001 && item.QTY > 0" class="custom-control custom-checkbox" v-on:click="singleCheckRecord(item)">
                                                <input class="chkItems custom-control-input" type="checkbox" name="checkbox" style="display: none;" v-model="item.isSelectProduct">
                                                <label class="custom-control-label universal-custom-control-label pt-1"></label>
                                            </div>
                                            <div v-else class="custom-control custom-checkbox">
                                                <input class="chkItems custom-control-input" disabled="disabled" type="checkbox" name="checkbox" style="display: none;" v-model="item.isSelectProduct">
                                                <label class="custom-control-label universal-custom-control-label pt-1 "></label>
                                            </div>
                                        </td>

                                        <td>{{item.USER_NAME}}</td>
                                        <td>{{item.NAME}}</td>
                                        <td>{{item.UNIQUE_NAME}}</td>
                                        <td>{{item.RETURN_REASON_NAME}}</td>
                                        <td>{{item.COMMENTS}}</td>
                                        <td>{{item.APPROVAL_COMMENT}}</td>
                                        <td v-if="item.STATUS_ID==1001" class="text-center single-action">
                                            <em class="active-disabled" statusid="1001">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-success">{{item.STATUS_NAME}}</span>
                                            </em>
                                        </td>
                                        <td v-else-if="item.STATUS_NAME=='Approved'" class="text-center single-action">
                                            <em class="active-disabled" statusid="1001">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-success">{{item.STATUS_NAME}}</span>
                                            </em>
                                        </td>
                                        <td v-else-if="item.STATUS_ID==1002 || item.STATUS_ID==1003" class="text-center single-action">
                                            <em class="active-disabled" statusid="1001">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-success">{{item.STATUS_NAME}}</span>
                                            </em>
                                        </td>
                                        <td v-else class="text-center single-action">
                                            <em class="active-disabled" statusid="1001">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-success">{{item.STATUS_NAME}}</span>
                                            </em>
                                        </td>
                                        <td class="text-center single-action">
                                            <i class="fa  fa-shopping-basket" aria-hidden="true"></i>
                                            <span class="noti-circle noti-blue" title="Quantity">{{item.QTY}}</span>
                                        </td>
                                        <td>{{item.ITEM_COST}}</td>
                                        <td>{{item.BOOK_VALUE}}</td>
                                        <td>

                                            <span v-if="item.STATUS_ID == 1001">{{item.totalDaysDue}}</span>
                                            <span v-else>{{item.GraceOverdueDays}}</span>
                                        </td>
                                        <td>
                                            <select v-if="item.StatusId == 1001 && item.QTY > 0 && item.IS_PENALTY == 1">
                                                <option></option>
                                            </select>
                                        </td>

                                        <td>
                                            <span v-if="item.STATUS_ID==1001">{{ totalDaysDue }}</span>
                                            <span v-else>{{item.GRACE_OVERDUE_DAYS}}</span>
                                        </td>
                                        <td>
                                            <select v-if="item.STATUS_ID==1001 && item.NUMBER_OF_ITEMS >0 && item.IS_PENALTY==1" name="StatusID" v-model="StatusID" v-on:click="GetAssetstatusList()" v-bind:class="{ 'form-control w-100': true}">
                                                <option value="">Select</option>
                                                <option v-for="(item, itemIndex) in StatusData" :key="itemIndex" :value="item.value">{{ item.name }}</option>
                                            </select>
                                            <span v-else-if="item.STATUS_ID != 1001">
                                                <span v-if="item.PenaltyStatusType==0">N/A</span>
                                                <span v-else>{{item.PenaltyStatusType}}</span>
                                            </span>
                                            <span v-else>N/A</span>
                                        </td>

                                        <td>
                                            <input v-if="item.STATUS_ID == 1001 && item.QTY > 0 && (item.IS_PENALTY == 1 || ISOVERDUE == true)" type="text" class="form-control" />
                                            <template v-else-if="item.STATUS_ID != 1001">
                                                <span v-if="item.PENALITY_AMOUNT < 0">N/A</span>
                                                <span v-else>{{item.PENALITY_AMOUNT}}</span>
                                            </template>
                                            <span v-else>N/A</span>
                                        </td>
                                        <td>{{ $options.filters.formatDateTime(item.ReplaceRequestDate) }}</td>
                                    </tr>

                                    <tr v-if="noRecord">
                                        <td class="text-center text-danger p-4 border no-record" colspan="20">{{$t('NoRecordfound')}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <div id="dvReturnStatus" class="form-content mt-2 p-3" style="width:1200px !important;">
                    <Form ref="ApproveRejectForm">
                        <div class="collapse show py-3">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                                <div class="form-group">
                                    <span>Status:<span class="text-danger">*</span></span>
                                    <Field name="Status" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                        <select name="Status" v-model="txtStatus" class="form-control" maxlength="150" v-bind:class="{'form-control': true, 'is-invalid': errors[0]}">

                                            <option v-for="(item, itemIndex) in statusvalue" :key="itemIndex" :value="item.ID">{{item.Text}}</option>
                                        </select>
                                        <span v-if="errors[0]" name="Status" class="invalid-feedback d-block">{{errors[0] }}</span>
                                    </Field>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                                <div class="form-group">
                                    <span>Comment:</span>
                                    <textarea name="txtComment" v-model="txtComment" class="form-control" maxlength="400"></textarea>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                                <div class="row flex-row-reverse">
                                    <div class="col-lg-6 text-right">
                                        <a href="javascript:;" class="btn btn-success formbtn text-white mr-1" @click="SubmitStatus()" type="submit" :title="$t('Save')" :disabled="noRecord"><i class="fa fa-save pr-2"></i>
                                            <span>{{$t('Save')}}</span></a>
                                        <a href="javascript:;" @click="onCancel()" :title="$t('Cancel')" class="btn btn-danger formbtn text-white"><i class="fa fa-close pr-2"></i><span>{{$t('Cancel')}}</span></a>
                                    </div>
                                    <div class="col-lg-6">
                                        <div class="py-2">
                                            <small class="text-danger">Fields marked with an asterisk (*) are mandatory.</small>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </Form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../service/DataService';

export default {

    props: {
        assDetailIDs: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            AssignmentData: [],
            txtComment: '',
            txtStatus: '',
            statusvalue: [{
                    ID: "Approve",
                    Text: "Approve"
                },
                {
                    ID: "Reject",
                    Text: "Reject"
                },
            ],
            AssetReplacelistData: [],
            SortBy: '',
            SortExp: '',
            PageSizees: 10,
            PageNumberes: 1,
            CreatedBy: '',
            relatedInfoID: '',
            username: '',
            searchStatus: '',
            PenaltyOnStatus: '',
            PenaltyOnOverdue: '',
            SelectAll: false,
            suser: [],
            mulipleSelect: false,
            noRecord: false,

        }
    },
    created: function () {
        this.GetApproveUnApprovelist();
    },
    methods: {
        CloseApproveRejectopup: function (item) {
            this.$emit('CloseApproveRejectopup', item)
        },
        onCancel: function () {
            this.CloseApproveRejectopup();
        },

        GetApproveUnApprovelist() {
            if (this.assDetailIDs) {
                var url = `sortBy=${this.SortBy}&sortExp=${this.SortExp}&pageSize=${this.PageSizees}&pageNum=${this.PageNumberes}&createdby=${this.CreatedBy}&relatedInfoID=${this.assDetailIDs}&username=${this.username}&searchStatus=${this.searchStatus}&PenaltyOnStatus=${this.PenaltyOnStatus}&PenaltyOnOverdue=${this.PenaltyOnOverdue}`;
            } else {
                var url = `sortBy=${this.SortBy}&sortExp=${this.SortExp}&pageSize=${this.PageSizees}&pageNum=${this.PageNumberes}&createdby=${this.CreatedBy}&relatedInfoID=${this.relatedInfoID}&username=${this.username}&searchStatus=${this.searchStatus}&PenaltyOnStatus=${this.PenaltyOnStatus}&PenaltyOnOverdue=${this.PenaltyOnOverdue}`;
            }
            DataService.GetAssetReturnRequest(url).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0 && response.data != '[]') {
                        this.AssetReplacelistData = JSON.parse(response.data);
                        this.TotalRecordes = response.data[0].TOTAL_PAGES;
                        this.TotalPagees = Math.ceil(this.TotalRecordses / this.PageSizees);
                        this.CurrentPagees = this.PageNumberes;
                        this.noRecord = false;
                    } else {
                        this.noRecord = true;
                        this.AssetReplacelistData = [];
                        this.TotalRecorded = 0;
                    }
                }
            });

        },
        singleCheckRecord: function (item) {
            var vm = this;
            vm.SelectAll = true;
            item.isSelectProduct = !item.isSelectProduct;
            if (item.isSelectProduct) {
                vm.AssetReplacelistData.forEach(element => {
                    if (!element.isSelectProduct) {
                        vm.SelectAll = false;
                    }
                })
            } else {
                vm.SelectAll = false;
            }
        },
        checkSelectedRecord: function () {
            var vm = this;
            vm.SelectAll = !vm.SelectAll
            if (vm.SelectAll) {
                vm.AssetReplacelistData.forEach(element => {
                    element.isSelectProduct = true;
                })
            } else {
                vm.AssetReplacelistData.forEach(element => {
                    element.isSelectProduct = false;
                })
            }
        },

        SubmitStatus() {
            var vm = this;          
            var val = [];
            //debugger
            vm.$refs.ApproveRejectForm.validate().then(result=>{
                if(result){
            if (vm.txtStatus == '' || val.length > 0) {                
                return false;                
            }
            vm.isLoading = true;
            val = vm.AssetReplacelistData.filter(element => element.isSelectProduct == true)
            const obj = {
                RequestData: val,
                RetrunRequeststatus: (vm.txtStatus != '') ? ((vm.txtStatus == 'Approve') ? 'ACCEPT' : 'REJECT') : '',
                comment: vm.txtComment
            }
        if(obj==null || obj.RequestData.length==0){
            vm.ShowAlert(vm.$t('PleaseSelectAtleastOneProduct'), "failure", true, vm.$t("Alert"));
                vm.isLoading=false; 
                     return false;
            }

            //var url = `RequestData=${val}&RetrunRequeststatus=${vm.txtStatus}&comment=${vm.txtComment}`;
            DataService.AddRejectStatus(obj).then(response => {
                debugger
                if (response.data.msg == 'ReturnedSuccessfully') {
                    if (vm.txtStatus == 'Approve') {
                        vm.ShowAlert(vm.$t('CommonApprove', [vm.$t('CRM_PRODUCTS')]), "success", true, vm.$t("Alert"));
                        vm.isLoading = false;
                        vm.$parent.FetchData();
                    } else {
                        vm.ShowAlert(vm.$t('CommonReject', [vm.$t('CRM_PRODUCTS')]), "success", true, vm.$t("Alert"));
                        vm.isLoading = false;
                        vm.$parent.FetchData();
                    }
                } 
                else if (response.data.msg == 'RejectedSuccessfully') {
                    if (vm.txtStatus == 'Approve') {
                        vm.ShowAlert(vm.$t('CommonApprove', [vm.$t('CRM_PRODUCTS')]), "success", true, vm.$t("Alert"));
                        vm.isLoading = false;
                        vm.$parent.FetchData();
                    } else {
                        vm.ShowAlert(vm.$t('CommonReject', [vm.$t('CRM_PRODUCTS')]), "success", true, vm.$t("Alert"));
                        vm.isLoading = false;
                        vm.$parent.FetchData();
                    }
                }
                else if (response.data.result == 'NotReturnedSuccessfully') {
                    if (vm.txtStatus == 'Approve') {
                        vm.isLoading = false;
                        vm.ShowAlert(vm.$t('CommonApprove', [vm.$t('CRM_PRODUCTS')]), "failure", true, vm.$t("Alert"));
                        vm.$parent.FetchData();
                    } else {
                        vm.isLoading = false;
                        vm.ShowAlert(vm.$t('CommonReject', [vm.$t('CRM_PRODUCTS')]), "failure", true, vm.$t("Alert"));
                        vm.$parent.FetchData();
                    }
                } else {
                    vm.ShowAlert(vm.$t('ReturnError'), "warning", vm.$t('Alert'));
                    vm.isLoading = false;
                }
            });
        }
    })
    },

        // SubmitData: function () {
        //     var vm = this;
        //     var detail = "",
        //         realtedinfoIDs = "",
        //         status = true,
        //         qty = '';
        //     vm.$refs.ReturnForm.validate().then(result => {
        //         if (result) {
        //             vm.AssetReplacelistData.forEach(ele => {
        //                 vm.qty = ele.NumberOfItemsToAssign;
        //                 vm.assDetailID = ele.AssignmentDetailId;
        //                 vm.userid = ele.IssuedToId;
        //                 vm.RelatedInfoID = ele.RelatedInfoID;
        //                 vm.NumberOfItemsToAssign = ele.NumberOfItemsToAssign;
        //                 vm.UniqueName = ele.UniqueName;
        //                 if (detail != "") detail += "|";
        //                 detail += vm.assDetailID + "," + vm.qty + "," + vm.userid;
        //                 if (realtedinfoIDs != "") realtedinfoIDs += "|";
        //                 realtedinfoIDs += vm.assDetailID + "," + vm.RelatedInfoID + "," + qty + "," + vm.UniqueName;
        //             });
        //             if (detail == "") {
        //                 vm.ShowAlert(vm.$t("Minimum One Product should be selected."), "warning", vm.$t('Alert'));
        //                 // hideLoader();
        //                 return false;
        //             }
        //             if (status == false) {
        //                 //  hideLoader();
        //                 return false;
        //             }
        //             var data = {
        //                 itemdetails: detail,
        //                 title: vm.txtTitle,
        //                 comment: vm.txtComment,
        //                 relatedInfoIDs: realtedinfoIDs
        //             }
        //             var jsonObj = JSON.stringify(data);
        //             DataService.BulkReturnItem(jsonObj).then(response => {

        //             });

        //         }
        //     })
        // }
    },

}
</script>
